 doute l’impression de solitude urbaine qui constitue la figure prédominante de l’impression paysagère montréalaise. Le regard des photographes ne s’intéresse pas aux perspectives globales du paysage urbain. Les cadrages photographiques ne sont pas englobants. Le paysage à voir n’est pas panoramique ni dominant. Par ce fait, ils démontrent l’importance de l’intériorité urbaine durant le confinement. L’impression de replis en est la figure clé du paysage urbain.")])]):_vm._e()])],1),_c('div',{staticClass:"center-col"},[_c('ChapterNav',{attrs:{"previous":true,"next":true}})],1)])}
